import React from "react"
import { Link } from 'react-router-dom';
import { appConfig } from '../../config/appConfig';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';

export default class Options extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <Breadcrumbs aria-label="breadcrumb">
                    <Link to="/help">
                        Help Center
                    </Link>
                    <Link to="/help/editing">
                        Editing
                    </Link>
                    <Link to="/help/cameraOptions">
                        General Settings
                    </Link>
                </Breadcrumbs>
                <h1>General settings</h1>
                <h2>Viewer Settings</h2>
                <img style={{ width: '300px' }} alt='shadows' src={require('assets/images/help/viewersettings.jpg')} />

                <h4>Auto Rotate</h4>
                <p>When selected the camera will rotate around the model.</p>

                <h4>Auto Rotate</h4>
                <p>Set the auto rotation speed.</p>

                <h4>Start Animation</h4>
                <p>When set the given animation will be played.</p>

                <h4>Loop Start Animation</h4>
                <p>The starting animation will loop when enabled.</p>

                <h4>Show Animation Control</h4>
                <p>Show/hide the animation control.</p>

                <h4>Show Dimensions Tool</h4>
                <p>
                    Show/hide the dimensions tool in the options menu.
                    These are project-specific viewer settings. See also the general viewer settings on the Integrate Viewer screen.
                </p>
        
                <h2>Project Image</h2>
                <img style={{ width: '300px' }} alt='shadows' src={require('assets/images/help/productimage.jpg')} />
                <h4>Taking a produc Image</h4>
                <p> Click the Capture Image button to create a HD image snapshot. This image will be used on the viewer loading screen.</p>
                <h4>Use WebAR</h4>
                <p>
                    Enable/disable WebAR mode of the viewer. When enabled this mode replaces the default AR mode which opens the scene in an AR application.<br />
                    This mode doesn&apos;t open an AR application and it supports the product configurator so users can customize a product in AR.<br />
                    This mode is only available for Android devices. The viewer will revert to the default AR mode on IOS devices.
                </p>

                <h2>AR and VR Settings</h2>
                <img style={{ width: '300px' }} alt='shadows' src={require('assets/images/help/arvr.jpg')} />
                <h4>Enable AR</h4>
                <p>Show/hide the AR button for the default AR mode. The default AR mode is to open a native AR mobile app when the button is clicked.</p>
                <h4>Enable WebAR</h4>
                <p>Show/hide the WebAR button.
                WebAR is a technology runs AR in the browser. 
                The main advantage of WebAR is the model doesn't have to be loaded again, which is the case with app-based AR.
                However WebAR on iOS has limited support, the Mozilla WebXR Viewer has to be downloaded.<br/>
                If you want to use the Product Configurator in AR use this mode.
                </p>
                <h4>Enable VR</h4>
                <p>Show/hide the VR button.</p>
                <h4>Walk speed</h4>
                <p>Change the user walk speed.</p>
                <h4>Stop walking when outside floor</h4>
                <p>
                    When this feature is enabled the user will stop when they walk outside of the detected floor mesh. 
                </p>
                <h3>Initial VR Position</h3>
                <p>
                    Set or clear the initial VR position and orientation in the scene. The default position is in front of the model, 1.6m above the ground.
                </p>
                <h4>Set to camera</h4>
                <p>
                    Set the VR position and orientation of the headset to that of the camera.
                    The height is set to 1.6m above the ground (y=0) or floor mesh. If the camera is directly above a floor mesh the height is set 1.6m above the floor mesh at that point.
                </p>
                <h4>Clear</h4>
                <p>
                    Clears the initial position and the default will be used.
                </p>
            </div>
        );
    }
}